<template>
    <v-container fluid fill-height style="padding:0;" id="class" grid-list-md>
        <Blog :items="items" :time="time" :title="title">
            <div class="blogcontent">
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-hover>
                        <v-card
                            slot-scope="{ hover }"
                            :class="`elevation-${hover ? 12 : 2}`"
                            min-height="200"
                        >
                            <v-card-title>
                            <h3 id="class">Class</h3>
                            </v-card-title>
                            <v-divider></v-divider>
                            <v-card-text>
                                <p class=".body-1">1. ES6提出的Class其实是原型继承的一种封装，本质上还是prototype继承</p>
                                <pre class="code">{{pres[0]}}</pre>

                                <p class=".body-1">2. ES6提出的Class功能比较简单
                                    <ul>
                                        <li>可以使用<code>extends</code>继承父类</li>
                                        <li>可以在构造函数中使用<code>super</code>调用父类的方法和属性</li>
                                        <li>可以设置<code>get</code>和<code>set</code>，这和对象中的是一样的</li>
                                        <li>可以使用<code>static</code>关键字声明静态属性和方法</li>
                                    </ul>
                                </p>
                                <pre class="code">{{pres[1]}}</pre>
                            </v-card-text>
                        </v-card>
                        </v-hover>
                    </v-flex>
                </v-layout>
            </div>
        </Blog>
    </v-container>
</template>

<script>
import Blog from "../Blog.vue";
export default {
    components: { Blog },
    data() {
        return {
            time: "2019-1-25",
            title: "解构赋值",
            items: [
                {
                    title: "Class",
                    target: "#class"
                },
            ],
            pres: 
[
`
    // 类声明的两种方式

    // 1. class语句形式
    class Myclass {
        constructor(a, b) {
            this.a = a;
            this.b = b;
        }
    }
    const mc = new Myclass('aa', 'bb');

    // 2. 表达式形式
    const Myclass = class {
        constructor(a, b) {
            this.a = a;
            this.b = b;
        }
    }
    const mc = new Myclass('aa', 'bb');
    
`,
`
    class Parent {
        constructor(a, b) {
            this.a = 'aa';
            this.b = 'bb';
        }

        static fn() { // 只能通过类本身调用
            console.log("我是静态的");
        }
    }

    class Child extends Parent { // extends作用实际上是将Child的原型对象设置成Parent
        constructor(a, b, c) { // 构造函数只能有一个，不指定默认也是有一个
            super(a, b); // super 调用父类的构造函数，只能在构造函数中使用，并且必须放在this的前面
            this.c = 'cc';
        }

        get ab() { // 获取
            return this.a + this.b;
        }

        set cv(value) { // 设置c的值
            this.c = value;
        }
    }

    const cd = new Child('aa', 'bb', 'cc');
    
    console.log(cd); // Child {a: "aa", b: "bb", c: "cc"}
    console.log(cd.ab); // "aabb"
    cd.c = "ccc";
    console.log(cd.c); // "ccc"
    Parent.fn();
    Child.fn();
`
],
        };
    },
}
</script>   

